.container {
	display: grid;
	place-items: center;
	position: fixed;
	min-height: 100vh;
	min-width: 99vw;
	background-color: black;
}

.loader {
	width: 4rem;
	height: 4rem;
	border-top: 3px solid blue;
	border-radius: 50%;
	animation: spin 1.5s linear infinite;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
} ;
